import React, { useState } from 'react';
import './InsuranceDetail.scss'

function InsuranceDetail() {

    const[insurance, setinsurance] = useState([
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228038/New-home-screen/Group_7175_adtido.svg',
            desc: 'Group health, accidental, and term life insurance with COVID-19 cover offered through insurance partners '
        },
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228040/New-home-screen/Group_7176_uzhutw.svg',
            desc: 'Family inclusive plans where you can add parents, siblings, in-laws, and children'
        },
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228046/New-home-screen/Group_7180_ifbkvf.svg',
            desc: 'Complete hospitalization assistance and claim support for Onsurity members '
        }
    ])

    const[insurancedesc, setinsurancedesc] = useState([
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228075/New-home-screen/Group_7177_l7kk5p.svg',
            desc: 'Reimbursements related to COVID-19 home healthcare expenses'
        },
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228054/New-home-screen/Group_7179_zqfep7.svg',
            desc: 'Discounted online medicine delivery and health check-ups for Onsurity members'
        },
        {
            icon: 'https://res.cloudinary.com/onsurity/image/upload/v1623228059/New-home-screen/Group_7178_o86lks.svg',
            desc: 'Free doctor teleconsultation with every Onsurity membership plan'
        }
    ])

    return (

    <>
        <div className="insur-maincls">
            <div className="insu-header" style={{paddingTop: '60px'}}>Features that make us unique</div>
            <div className="insudetails-sec">
                {insurance.map(e=>(
                    <div style={{marginTop: '40px'}}>
                        <div align="center">
                            <img src={e.icon} className="insu-imgheight" />
                        </div>
                        <div className="insu-descr">{e.desc}</div>
                    </div>
                ))}

            </div>
            </div>

        <div className="insurr-secndcls">
            <div className="sec-insudetails-sec">
                    {insurancedesc.map(e=>(
                        <div style={{marginTop: '40px'}}>
                            <div align="center">
                                <img src={e.icon} className="insu-imgheight" />
                            </div>
                            <div className="insu-descr">{e.desc}</div>
                        </div>
                    ))}

                </div>
        </div>
         

    </>
           
        
    );
}

export default InsuranceDetail;