import React from 'react'

export default function BillingWork(props) {
    return (
        <div>
            {localStorage.getItem('channel') && localStorage.getItem('channel')=="magicpin" ?
            <>
                <img className="WebhowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1621417464/OnSurity/Group_6218_t3ty3g.svg" />
                <img className="MobilehowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1621417432/OnSurity/Group_6219_bbjlvz.svg" />
            </>
            :
            <>
            {props.type=="annual"?
            <>
            <img className="WebhowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599843571/OneTimeWeb_n94vmk.svg" />
            <img className="MobilehowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1599843571/OneTimeMob_znwrs6.svg" />
            </>
            :props.type=="monthly" &&
            <>
            <img className="WebhowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1630309508/New-home-screen/Group_6233_cgq1nx.svg" />
            <img className="MobilehowWorks" width="100%" src="https://res.cloudinary.com/onsurity/image/upload/v1630310086/New-home-screen/Group_6100_sn6swj.svg" />
            </>}
            </>
            }
        </div>
    )
}
